import { __decorate } from "tslib";
import { Vue, Component, Mixins } from 'vue-property-decorator';
import MessageBox from '@/components/MessageBox.vue';
import { bannersMapper } from '@/modules/banners/module/store';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
import MsgBoxTools from '@/modules/banners/mixins/MsgBoxTools';
const BannersMappers = Vue.extend({
    computed: {
        ...bannersMapper.mapState(['list']),
        ...bannersMapper.mapGetters(['isLoading'])
    },
    methods: {
        ...bannersMapper.mapMutations(['clearForm']),
        ...bannersMapper.mapActions(['loadGlobalData', 'getList'])
    }
});
let ModuleBanners = class ModuleBanners extends Mixins(BannersMappers, MsgBoxTools, MsgBoxToolsApp) {
    constructor() {
        super(...arguments);
        this.secondBtn = { type: 'success', isPlain: true };
    }
    get failedFetchList() { return this.requestStatus === 'failFetchList'; }
    created() {
        this.loadData();
        this.clearForm();
    }
    goToPageApp() { this.$router.push({ path: '/' }); }
    goToPageAuth() { this.$emit('goToPageAuth'); }
    loadData() {
        if (this.list.isLoading)
            return;
        if (this.msgBoxIsShown)
            this.closeMsgBox();
        this.loadGlobalData()
            .catch((err) => {
            if (err && err.status && err.status.toString().slice(0, 2) == 40)
                this.goToPageAuth();
            else {
                this.requestStatus = 'failFetchList';
                this.openMsgBox();
            }
        });
    }
};
ModuleBanners = __decorate([
    Component({
        components: {
            MessageBox,
        }
    })
], ModuleBanners);
export default ModuleBanners;
